@use "util.scss" as *;

.triplist-page {
  font-family: "malssami815";
  width: 100%;
  margin-bottom: 10rem;
  margin-bottom: 320px;

  .more-btn {
    @include rounded-blue;
  }

  .big-section-title {
    font-size: 1.5rem;
    width: 100%;
    text-align: left;
    border-bottom: 2px solid #000;
    margin-bottom: 10px;
    padding-bottom: 10px;
  }
}

.trip-list {
  width: 100%;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.trip-item {
  width: 400px;
  max-height: 20%;
  border: 2px solid #000;
  border-radius: 10px;
  margin: 10px;
  position: relative;
  text-align: left;

  .content-wrapper {
    width: 70%;
    min-height: 100px;
    margin: 10px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-bottom: 10px;

    .desc-text {
      font-family: "NanumSquareNeo";
    }

    .profile-container {
      position: absolute;
      right: 0%;
      top: 50%;
      transform: translateY(-50%);
      margin-right: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;

      img {
        width: 70px;
        height: 70px;
        border-radius: 50px;
        margin-bottom: 10px;
      }
    }

    .text-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: left;
    }

    .title-text {
      margin-bottom: 10px;
      display: flex;
      align-items: center;

      span {
        font-family: "BMJUA";
        font-size: 1.5rem;
        margin-right: 10px;
        cursor: pointer;
      }

      img {
        width: 20px;
      }
    }
  }

  .downside {
    margin: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      color: gray;
    }

    img {
      width: 25px;
      height: auto;
      margin-right: 5px;
    }

    div {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
  }

  .back-img {
    width: 100%;
    height: 110px;
    border-radius: 8px 8px 0 0;
  }
}
